<template>
  <v-footer app inset padless absolute width="auto">
    <v-card class="flex" flat tile>
      <v-card-title class="black">
        <v-spacer></v-spacer>
        <h2 class="hover-effect">
          <span @click="replaceEmoji()" id="emoji">👀</span>
        </h2>
        <v-spacer></v-spacer>
      </v-card-title>
    </v-card>
  </v-footer>
</template>
<script>
export default {
  name: "Footer",

  data: () => ({
    hand: false,
  }),
  methods: {
    replaceEmoji() {
      document.getElementById("emoji").innerHTML = this.hand ? "👀" : "👋";
      this.hand = !this.hand;
    },
  }
};
</script>

<style>
.hover-effect:hover {
  cursor: grab;
}
</style>