<template>
  <div ref="expandedItem" class="root">
    <v-card elevation="2" :ripple="false" @click="openDescription(description, url)" class="custom-card"
      :style="getStyle()" :width="getMaxWidth()" dark>
      <div class="d-flex" style="position: relative; background-color: rgba(0, 0, 0, 0.25);">
        <v-avatar :size="getAvatarSize()" class="ma-3" tile>
          <v-img :src="imageUrl" class="avatar-image"></v-img>
        </v-avatar>

        <v-col class="text-column">
          <v-text :class="getTitleClass()" v-text="title"></v-text>
          <v-text :class="getSubtitleClass()" v-text="subtitle"></v-text>
        </v-col>

        <div style="bottom: 6px; right: 8px; position: absolute;">
          <v-icon class="text-with-shadow-2" size="27">{{ show ? 'mdi-chevron-up' : 'mdi-chevron-down' }}</v-icon>
        </div>
      </div>
      <v-expand-transition>
        <div v-show="show">
          <div style="background-color: rgba(0, 0, 0, 0.7); backdrop-filter: blur(24px);">
            <v-card-text :class="getDescriptionClass()" v-html="formatMultilineString(description)"></v-card-text>
          </div>
          <v-card-actions style="background-color: rgba(0, 0, 0, 0.25);">
            <v-chip v-for="(url, index) in urls" :key="index" @click.stop="openUri(url.url)" class="ma-2"
              style="background-color: rgb(220, 220, 220); color: black;">
              <v-icon style="color: black;" left>mdi-link</v-icon>
              {{ url.actionLabel }}
            </v-chip>
          </v-card-actions>
        </div>
      </v-expand-transition>
    </v-card>
  </div>
</template>

<script>

export default {
  components: {},
  name: 'ProjectCard',
  props: {
    title: String,
    subtitle: String,
    description: String,
    urls: {
      url: String,
      actionLabel: String,
    },
    actionLabel: String,
    imageUrl: String,
    backgroundImage: String,
    gradientStartColor: {
      type: String,
      default: '#ffffff',
    },
    gradientEndColor: {
      type: String,
      default: '#000000',
    },
  },
  data() {
    return {
      show: false,
      screenWidth: window.innerWidth,
    };
  },
  mounted() {
    window.addEventListener("resize", this.handleResize);
    this.handleResize();
  },
  beforeDestroy() {
    window.removeEventListener("resize", this.handleResize);
  },
  methods: {
    handleResize() {
      this.screenWidth = window.innerWidth;
    },
    formatMultilineString(text) {
      return text.replace(/\\n/g, '\n').replace(/\n/g, '<br>');
    },
    openDescription(description, url) {
      if (description === undefined) {
        this.openUri(url);
        return;
      }
      this.show = !this.show;

      if (this.show) {
        setTimeout(() => {
          this.$refs.expandedItem.scrollIntoView({
            block: 'center',
            behavior: 'smooth'
          });
        }, 125);
      }

      setTimeout(() => {
        document.activeElement.blur();
      }, 125);
    },
    openUri(uri) {
      window.open(uri, uri);

      setTimeout(() => {
        document.activeElement.blur();
      }, 125);
    },
    getGradientBackground() {
      return this.backgroundImage ? null : `linear-gradient(to right, ${this.gradientStartColor}, ${this.gradientEndColor})`;
    },
    getStyle() {
      if (!this.backgroundImage) {
        return {
          background: this.getGradientBackground()
        };
      }

      return {
        backgroundImage: `url(${this.backgroundImage})`,
      };
    },
    getAvatarSize() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 96;
        case "sm":
        case "md":
        case "lg":
        case "xl":
        default:
          return 125;
      }
    },
    getMaxWidth() {
      if (this.screenWidth < 600) {
        return this.screenWidth - 48;
      }

      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return 300;
        case "sm":
          return 320;
        case "md":
          return 380;
        case "lg":
        case "xl":
        default:
          return 460;
      }
    },
    getTitleClass() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "text-h5 text-with-shadow";
        case "sm":
          return "text-h5 text-with-shadow";
        case "md":
          return "text-h4 text-with-shadow";
        case "lg":
        case "xl":
        default:
          return "text-h4 text-with-shadow";
      }
    },
    getSubtitleClass() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "text-body-2 text-with-shadow-2";
        case "sm":
        case "md":
        case "lg":
        case "xl":
        default:
          return "text-body-1 text-with-shadow-2";
      }
    },
    getDescriptionClass() {
      switch (this.$vuetify.breakpoint.name) {
        case "xs":
          return "text-body-2";
        case "sm":
        case "md":
        case "lg":
        case "xl":
        default:
          return "text-body-1";
      }
    },
  },
};
</script>

<style scoped>
.text-with-shadow {
  text-shadow: 1px 1px 4px rgba(0, 0, 0, 0.6);
}

.text-with-shadow-2 {
  text-shadow: 1px 1px 2px rgba(0, 0, 0, 0.5);
}

.custom-card {
  border-radius: 18px;
  background-size: cover;
  background-position: center;
}

.avatar-image {
  border-radius: 18px;
}

.text-column {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  z-index: 1;
}

pre {
  font-size: inherit;
  color: inherit;
  border: initial;
  padding: initial;
  font-family: inherit;
}
</style>